.SearchBar {
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-image: url('../../img/foodBackground-2.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 35rem;
	z-index: -1;
}

.overlay-image {
	width: 100%;
	height: 50%;
	background: #000;
	opacity: 0.3;
	position: absolute;
}

.SearchBar-sort-options ul {
	display: flex;
	justify-content: center;
	margin-bottom: 2.22rem;
	color: #ffffff;
	z-index: 1;
}

.SearchBar-sort-options li {
	cursor: pointer;
	width: 4.33rem;
	border-bottom: 1px solid #fff;
	padding: 0 2.58rem 0.33rem 2.58rem;
	line-height: 1.13;
	text-align: center;
	font-weight: 600;
	font-size: 0.83rem;
	transition: color 0.25s;
	z-index: 1;
}

.SearchBar-sort-options li:hover {
	color: #d4cfcf;
	z-index: 1;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
	border-bottom: 1px solid #f0c36c;
	color: #f0c36c;
	z-index: 1;
}

.SearchBar-fields {
	display: flex;
	justify-content: center;
	margin-bottom: 2.88rem;
	z-index: 1;
}

.SearchBar-fields input {
	width: 21rem;
	padding: 0.66rem 1rem;
	margin-right: 2.22rem;
	border: 1px solid #fff;
	border-radius: 4px;
	font-size: 0.77rem;
	font-weight: 500;
	z-index: 1;
}

.SearchBar-fields input:last-child {
	margin-right: 0;
	z-index: 1;
}

.SearchBar-submit {
	text-align: center;
	z-index: 1;
}

.SearchBar-submit a {
	border-radius: 4px;
	padding: 0.72rem 1.7rem;
	background-color: #cca353;
	color: #ffffff;
	font-weight: 600;
	transition: background-color 0.5s;
	z-index: 1;
}

.SearchBar-submit a:hover {
	cursor: pointer;
	background-color: #a7874b;
	z-index: 1;
}

@media only screen and (max-width: 560px) {
	.SearchBar {
		background-image: url('../../img/foodBackground-3.jpg');
	}

	.SearchBar-sort-options ul {
		margin-left: 2rem;
		margin-right: 2rem;
	}

	.SearchBar-fields {
		flex-direction: column;
		align-items: center;
	}

	.SearchBar-fields input {
		margin-right: 0;
		margin-bottom: 0.86rem;
	}

	.overlay-image {
		width: 100%;
		height: 50%;
		background: #000;
		opacity: 0.3;
		position: absolute;
	}
}
